body {
  font-family: "futura-demic-reg", Arial, sans-serif;
  font-size: 11px;
  font-style: normal;
  letter-spacing: .1em;
  padding: 11px 0;
  background: white;
}

.menuLogo {
  text-align: center;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.language li {
  list-style-type: none;
}

.language {
  text-align: right;
}

.language__item li {
  display: inline-block;
}

.language__item-ru {
  font-weight: bold;
}

.language__item-ua a {
  color: #333;
}

.language__item-ua a:hover {
  color: #333;
  text-decoration: none;
}

@media (max-width: 576px) {
  .language ul {
    text-align: center;
    padding-left: 0;
    padding-top: 10px;
  }
}

.social ul {
  list-style-type: none;
  text-align: left;
  padding: 0;
}

@media (max-width: 576px) {
  .social ul {
    text-align: center;
  }
}

.social li {
  display: inline-block;
}

.social a {
  color: #6D6E70;
}

.social i {
  font-size: 1.1rem;
  padding: 0 5px;
}

.social a:hover {
  color: #6D6E70;
}

header {
  padding-top: 28px;
}

.container {
  width: 100%;
  max-width: 1180px !important;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.homePage a:hover {
  text-decoration: none;
  color: #000;
}

.homePage span {
  color: #000;
  font-size: 12px;
}

.navigation {
  border-bottom: 1px solid black;
  padding-bottom: 10px;
  margin-bottom: 30px;
}

.navigation ul {
  width: 100%;
  list-style-type: none;
  padding-top: 30px;
  padding-left: 0px;
  font-size: 11px;
  text-align: center;
  color: #000;
}

.navigation li {
  padding: 0 20px;
  display: inline-block;
}

.navigation li a:hover {
  text-decoration: none;
  color: #000;
}

.navigation span {
  color: #000;
}

.menu {
  padding-bottom: 30px;
}

.content {
  width: 100%;
  max-width: 1180px !important;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 576px) {
  .content {
    padding-right: 30px;
    padding-left: 30px;
  }
}

.nextPage {
  display: flex;
  font-size: 11px;
}

@media (max-width: 576px) {
  .nextPage {
    padding-bottom: 16px;
  }
}

.nextPage p {
  margin: 1px;
}

.nextPage__prev {
  color: #6D6E70;
}

.nextPage__prev:hover {
  color: #6D6E70;
  text-decoration: none;
}

.nextPage__portfolio {
  color: black;
}

.nextPage__portfolio:hover {
  text-decoration: none;
  color: black;
}

.titleContent {
  font-size: 10px;
  text-align: center;
  font-weight: bold;
  padding-top: 10px;
}

.titleContent p {
  margin-bottom: 2rem;
}

.portfolio__photo {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  z-index: 5;
}

@media (max-width: 768px) {
  .portfolio__photo {
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    margin: 0 auto;
  }
}

.buttonBox {
  position: relative;
  margin: 0 auto;
  margin-bottom: 30px;
  margin-top: 10px;
}

button {
  border: 1px solid #000000;
  color: inherit;
  background-color: transparent;
  font-family: "futura-demic-reg", Arial, sans-serif;
  font-size: 11px !important;
  font-style: normal;
  letter-spacing: .1em;
  padding: 7px 14px;
}

.buttonBox button:focus {
  outline: none;
}

.border {
  position: absolute;
  transition: all .5s ease-in-out;
}

#fourth > .border:nth-of-type(1) {
  content: "";
  position: absolute;
  left: 0;
  height: 0;
  width: 0;
  background-color: #c7c4c5;
}

#fourth > .border:nth-of-type(2) {
  content: "";
  position: absolute;
  right: 0;
  height: 0;
  width: 0px;
  background-color: #c7c4c5;
}

#fourth > .border:nth-of-type(3) {
  content: "";
  bottom: 0;
  right: 0;
  width: 0px;
  height: 0;
  background-color: #c7c4c5;
}

#fourth > .border:nth-of-type(4) {
  content: "";
  bottom: 0;
  left: 0;
  width: 0;
  height: 0;
  background-color: #c7c4c5;
}

#fourth:hover .border:nth-of-type(1) {
  width: 100%;
}

#fourth:hover .border:nth-of-type(2) {
  height: 30px;
}

#fourth:hover .border:nth-of-type(3) {
  width: 100%;
}

#fourth:hover .border:nth-of-type(4) {
  height: 30px;
}

.photo {
  flex: 0 0 31%;
  margin: 10px;
}

@media (max-width: 576px) {
  .photo {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

@media (max-width: 768px) and (min-width: 577px) {
  .photo {
    flex: 0 0 31%;
    max-width: 100%;
  }
}

.allContent {
  padding: 0 60px;
  font-size: 14px;
  letter-spacing: .1em;
}

@media (max-width: 576px) {
  .allContent {
    padding: 0;
  }
}

.allContent p {
  text-indent: 40px;
  line-height: 1.6em;
  font-family: "futura-demic-reg", Arial, sans-serif;
  font-style: normal;
  letter-spacing: .1em;
}

.allContent h6 {
  border-bottom: 1px solid #ccc;
  padding-top: 40px;
  font-weight: bold;
  letter-spacing: 0.1em;
}

@media (max-width: 576px) {
  .allContent h6 {
    padding: 0;
    margin-top: 56px;
  }
}

.image {
  height: 450px;
  width: 100%;
  position: relative;
}

@media (max-width: 576px) {
  .image {
    height: 370px;
    margin: 0;
    width: 100%;
  }
}

.portfolio .portfolio__photo .background {
  opacity: 0;
  position: absolute;
  background: rgba(255, 255, 255, 0.8);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.year {
  text-align: center;
  position: relative;
  top: 50px;
  font-size: 14px;
  color: black;
}

.background .title {
  position: relative;
  font-size: 14px;
  color: black;
  text-align: center;
  top: 50%;
}

.photo:hover .background {
  opacity: 1;
  transition: opacity .7s;
  display: block;
  color: #000000;
}

.image:hover {
  opacity: 0.3;
  transition: opacity .7s ease-out;
}

.portfolio .portfolio__photo a {
  text-decoration: none;
  color: #000000;
  align-items: center;
  text-align: center;
  position: relative;
  display: block;
}

footer {
  width: 100%;
  max-width: 1180px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.footer__wrap {
  font-size: 13px;
  border-top: 1px solid #666;
  padding-top: 30px;
}

.footer__wrap ul {
  list-style: none;
  margin: 0 0 18px;
  padding: 0;
}

.footer__wrap a {
  color: #6D6E70;
}

.footer__wrap a:hover {
  color: #6D6E70;
  text-decoration: none;
}

.footer__title a {
  font-weight: bold;
  color: #000000 !important;
}

.footer__copyrite {
  text-align: right;
}

@media (max-width: 576px) {
  .footer__copyrite ul {
    text-align: center;
    margin: 15px 0 18px 0;
  }
}

@media (max-width: 576px) {
  .navigation {
    display: none;
  }
}

.burger__menu {
  display: none;
}

@media (max-width: 576px) {
  .burger__menu {
    display: block;
    padding-bottom: 12px;
    border-bottom: 1px solid black;
  }
}

.burger__menu ul {
  text-align: center;
  padding: 0;
  margin: 0;
}

.header__list li {
  list-style: none;
  margin: 0;
}

.header__link {
  text-transform: uppercase;
  color: black;
  position: relative;
  font-family: "futura-demic-reg", Arial, sans-serif;
  font-size: 11px;
  font-style: normal;
  letter-spacing: .1em;
}

.header__burger {
  display: block;
  position: relative;
  width: 60px;
  height: 32px;
  margin: 0 auto;
}

.header__burger span {
  position: absolute;
  width: 100%;
  background-color: #000;
  border-radius: 3px;
  height: 3px;
  left: 0;
  top: 14px;
  transition: cubic-bezier(0.8, 0.5, 0.2, 1.4);
  transition-duration: 150ms;
}

.header__burger::before,
.header__burger::after {
  content: '';
  position: absolute;
  width: 100%;
  background-color: #000;
  border-radius: 3px;
  height: 3px;
  left: 0;
  transition: cubic-bezier(0.8, 0.5, 0.2, 1.4);
  transition-duration: 150ms;
}

.header__burger::before {
  top: 0;
}

.header__burger::after {
  bottom: 0;
}

.header__menu {
  position: relative;
  top: -443px;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  padding: 15px 0 1px 0;
  transition: 7s;
}

.header__menu.active {
  display: block;
  top: 0;
  transition: 7s;
}

.header__burger.active:before {
  transform: rotate(45deg);
  top: 14px;
}

.header__burger.active:after {
  transform: rotate(-45deg);
  bottom: 14px;
}

.header__burger.active span {
  transform: scale(0);
}

